import Vue from 'vue';
import { Component, Watch } from 'vue-property-decorator';
import _ from 'lodash';
import * as d3 from 'd3';

@Component({
  components: {},
  props: {
    data: {
      type: Array,
      required: true,
    },
    yData: {
      type: Array,
      required: true,
    },
    graphName: {
      type: String,
      default: 'gg',
    },
  },
})
export default class LineGraph extends Vue {
  public $refs!: Vue['$refs'] & {
    container: HTMLElement;
  };

  public pathString: string = '';
  public svg: any;
  public lineGenerator = d3.line();

  public lineArr: any[] = [];
  public pointX: number = 0;
  public pointY: number = 0;
  public backgroundXArr: any[] = [];
  public backgroundYArr: any[] = [];
  public avgData: number[] = [0, 10, 20, 30, 40];
  public chartBox = {
    x: 50,
    y: 0,
    width: 10,
    height: 130,
  };

  public mounted() {
    this.pointY = this.$refs.container.clientHeight;
    this.reaplacePoints();
    this.replacePath();

    window.addEventListener('resize', () => {
      this.reaplacePoints();
      this.replacePath();
    });
  }

  private reaplacePoints() {
    this.pointX =
      (this.$refs.container.clientWidth - this.chartBox.x + 5) /
      this.$props.data.length;
  }

  private replacePath() {
    this.lineArr = [];
    _.forEach(this.$props.data, (data, i: number) => {
      this.lineArr.push([
        i * this.pointX + this.chartBox.x,
        this.pointY - data.value * 4 - 30,
      ]);
    });

    const pathString = this.lineGenerator(this.lineArr);
    if (!_.isNil(pathString)) {
      this.svg = d3.select(`#${this.$props.graphName}`);
      this.svg
        .select('#graphPath')
        .attr('d', pathString)
        .attr('fill', 'none')
        .style('stroke', 'hsla(0,0%,100%,.8)')
        .attr('stroke-width', '4');
    }
  }
}
