import Vue from 'vue';
import { Component, Watch } from 'vue-property-decorator';
import _ from 'lodash';
import * as d3 from 'd3';

@Component({
  components: {},
  props: {
    data: {
      type: Array,
      required: true,
    },
  },
})
export default class BarGraph extends Vue {
  public $refs!: Vue['$refs'] & {
    container: HTMLElement;
  };
  public show = false;
  public dataX: number = 0;
  public dataY: number = 0;
  public avgData: number[] = [0, 200, 400, 600, 800];
  public chartBox = {
    x: 50,
    y: 0,
    width: 10,
    height: 130,
  };

  public mounted() {
    this.dataX =
      (this.$refs.container.clientWidth - this.chartBox.x + 5) /
      this.$props.data.length;
    this.dataY = this.$refs.container.clientHeight;

    window.addEventListener('resize', () => {
      this.dataX =
        (this.$refs.container.clientWidth - this.chartBox.x + 5) /
        this.$props.data.length;
    });
  }
}
